@import "import/base";
@import "import/typography";
@import "import/reset-css";

@import "import/lib-fonts";

body{font-weight:400; color:#222; line-height:1; font-family:"Noto Sans KR", sans-serif; word-break:keep-all; background-color: #f6f6f6;}
select{
	&::-ms-expand{display:none;}
	&:focus{outline:none;}
	&::-ms-value{background:none;}
}
img{width: 100%;}
.wrap{position:relative; width:100%; min-width:rem(44.44vw); letter-spacing:-0.02em;}
.roboto{font-family:"roboto", "Noto Sans KR", sans-serif;}
