// =======================================
// Font Language
// =======================================
html {
  font-size: $html-fz;
  font-family: $font-family-ko;
  body * {
    line-height: 1em; }
  &[lang="ko-KR"], &[lang="ko"] {
    font-family: $font-family-ko;
    body {
      font-size: $body-fz;
      font-weight: 400; } }
  &[lang="en"], *[lang="en"] {
    font-family: $font-family-en;
    body {
      font-size: $body-fz + 0.28vw;
      font-weight: 400; } } }
