// =======================================
// Font Settings
// =======================================

// rem 단위 계산을 위한 html 사이즈 리셋
$html-fz: 2.22vw;

// 본문(paragraph) 사이즈
$body-fz: 2.22vw;

// 기본 행간 비율
$line-height: 1rem;

// 본문 행간 비율
$base-line: $body-fz * 1;

// 적용 웹 폰트
$font-family-ko: 'Noto Sans KR', sans-serif;
$font-family-en: 'Noto Sans KR', sans-serif;
